        <ul>
          <li>X: (-4 ... 4)</li>
          <li>Y: {-2, -1.5, -1, -0.5, 0, 0.5, 1, 1.5, 2}</li>
          <li>R: {1, 1.5, 2, 2.5, 3}</li>
        </ul>
      </div>

      <div class="content" id="overviewContent">
        <p>Author: Nguyen Ngoc Duc</p>
        <p>Group: P3233</p>
        <p>Github: <a href="https://github.com/ndwannafly">github.com/ndwannafly</a></p>
        <p>Contact:</p>
        <ul>
          <li>
