          <input type="radio" id="y_-0.5" name="y_value" value="-0.5" v-model="y_data">
          <label for="y_-0.5">-0.5</label>
          <input type="radio" id="y_0" name="y_value" value="0" v-model="y_data">
          <label for="y_0">0</label>
          <input type="radio" id="y_0.5" name="y_value" value="0.5" v-model="y_data">
          <label for="y_0.5">0.5</label>
          <input type="radio" id="y_1" name="y_value" value="1" v-model="y_data">
          <label for="y_1">1</label>
          <input type="radio" id="y_1.5" name="y_value" value="1.5" v-model="y_data">
          <label for="y_1.5">1.5</label>
          <input type="radio" id="y_2" name="y_value" value="2" v-model="y_data" checked>
          <label for="y_2">2</label>
        </div>
        <br>
        <p>
          Select R:
        </p>
        <div class="r-button-line">
          <input type="radio" id="r_1" name="r_value" value="1" v-model="r_data">
          <label for="r_1">1</label><br>
          <input type="radio" id="r_1_5" name="r_value" value="1.5" v-model="r_data">
          <label for="r_1_5">1.5</label><br>
          <input type="radio" id="r_2" name="r_value" value="2" v-model="r_data">
          <label for="r_2">2</label><br>
          <input type="radio" id="r_2_5" name="r_value" value="2.5" v-model="r_data">
          <label for="r_2_5">2.5</label><br>
          <input type="radio" id="r_3" name="r_value" value="3" v-model="r_data" checked>
          <label for="r_3">3</label><br>
        </div>

        <div class="button-group">
          <input class="button" type="submit" value="Submit" id="submit-button">
        </div>
      </div>
    </div>
