<template>
  <div>
      <div style="display: flex; flex-direction: row; align-items: center">
        <h3>username</h3>
        <input type="text" style="height: 20px; margin-left: 20px" v-model="username" id="username">
      </div>
      <div style="display: flex; flex-direction: row; align-items: center">
        <h3>password</h3>
        <input type="password" style="height: 20px; margin-left: 20px" v-model="password" id="password">
      </div>
      <div>
        <button style="margin-right: 20px" id="login">Login</button>
        <button id="signup">Signup</button>
      </div>
  </div>
</template>

<script>
export default {
  name: "Index",
  data() {
    return {
      username: "",
      password: ""
    }
  },
  mounted() {

    let usernameValidCheck = (username) => {
      let usernameRegex = /^[a-zA-z0-9]+$/;
      console.log("username: " + username);
      console.log("check: " + usernameRegex.test(username));
      return usernameRegex.test(username);
    }

    document.getElementById("signup").addEventListener("click", (e, i) => {
        //alert(usernameValidCheck(this.username));
        if(!usernameValidCheck(this.username)) {
          alert("invalid username format: should not contain special symbol")
          return;
        }

        fetch('https://itmoweblab4.herokuapp.com/register', {
            method: "POST",
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                "login": this.username,
                "password": this.password
              })

            /*headers: {
              'Access-Control-Allow-Origin' : '*',
              'Access-Control-Allow-Methods' : 'POST',
            }*/
        }).then(response => {
            if(response.ok){
                alert('Signup successfully!')
            }
            else {
              if(response.status === 404) alert('Can\'t connect to server');
              else alert('Account exists!')
            }
        }).catch(error => {
            alert(error);
        })
    });
    document.getElementById("login").addEventListener("click", (e, i) => {
      if(!usernameValidCheck(this.username)) {
        alert("invalid username format: should not contain special symbol")
        return;
      }

      fetch('https://itmoweblab4.herokuapp.com/login', {
        method: "POST",
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "login": this.username,
          "password": this.password
        })
      }).then(response => {
        console.log(response);
        if(response.ok){
          return response.json();
          /*localStorage.setItem('login', this.username);
          localStorage.setItem('password', response.password);
          this.$router.push({name: 'main-page'});*/
        } else{
            alert('login fail');
            return null;
        }
      }).then(result => {
          console.log(result);
          if(result != null){
            localStorage.setItem('login', result.login);
            localStorage.setItem('password', result.password);
            this.$router.push({name: 'main-page'});
          }
      })
          .catch(error => {
        console.log(error);
      })
    })
  }
}


</script>

